.footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
  }
  
  
  .footer .col{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
    margin-left: 50px
  
  }
  
  .footer .sec{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  
    
  }
  
  .footer .logo{
    margin-bottom: 20px;
  }
  
  .footer h4{
    font-size: 14px;
    padding-bottom: 20px
  }
  
  .footer p{
    font-size: 13px;
    margin: 0 0 8px 0;
  }
  
  
  .footer a{
    font-size: 13px;
    text-decoration: none;
    color: #222;
    margin-bottom: 10px;
  }
  
  .footer .follow{
    margin-top: 20px
  }
  
  .footer .follow i{
    color: #465b52;
    padding-right: 5px;
    cursor: pointer;
    
  }
  
  .footer .follow i:hover, .footer a:hover {
    color: #088178;
   
  }
  
  .footer .install .row img{
    border: 1px solid #088178; 
    border-radius: 6px;
      
  }
  
  .footer .install img{
    margin: 10px 0 15px 0
  }
  
  
  .footer .copyright{
    width: 100%;
    text-align: center
     
  }
  

  
  @media (max-width: 477px) {
    
    .footer .copyright{
      text-align: start;
    }
  }
  
  