

.featureBox{
    width:180px;
    text-align: center;
    padding: 25px 10px;
    box-shadow: 20px 20px 34px rgba(0,0,0,0.03);
    border: 1px solid #cce7d0;
    margin:15px 0;
    border-radius: 4px;
}
 .featureBox:hover{
    box-shadow: 10px 10px 54px rgba(70,62,221,0.1);
}
.featureBox img{
    margin-bottom: 10px;
    width:100%;
}
.featureBox h6{
    display: inline-block;
    padding: 9px 8px 6px 8px;
    line-height: 1;
    border-radius: 4px;
    color: #088178;
    background-color: #fddde4;
}
.featureBox h6{
    background-color: #cdebbc;
}
@media (max-width:799px) {

     .featureBox{
       margin:15px 15px;
    }
   

}

@media (max-width:477px){

    .featureBox{
            width: 155px;
    
    margin: 0 0 15px 0;
    }
    


}
